.App {
  padding: 36px;
  background: white;
}

.App-image {
  position: absolute;
  right: -480px;
  top: -500px;
  max-width: 1250px;
  width: auto;
  height: auto;
  animation: App-image-spin infinite 500s linear;
  pointer-events: none;
  z-index: 1;
  mix-blend-mode: normal
}

#shape1 {  
  position: absolute;
  right: 180px;
  top: 180px;
  /* z-index: 10; */
  width: 250px;
  height: 250px;
  animation: App-image-spin-counter infinite 120s linear;
  display: inline-block;
  mix-blend-mode: multiply;
  background-blend-mode: multiply;
  background-color: rgb(249, 212, 72);
}

.transparent {  
  position: absolute;
  animation: App-image-spin-counter infinite 30s linear;
  z-index: 10;
  display: inline-block;
  mix-blend-mode: multiply;
  background-blend-mode: multiply;
  background-color: rgb(249, 212, 72);
}

.App {
  margin-top: 100px;
}

.App-logo {
  text-transform: uppercase;
}

.App-subheading {
  margin-top: 6px;
  font-size: 18px;
  text-transform: uppercase;
}

.App-header {
  font-size: calc(10px + 2vmin);
}

/* Animations */
@keyframes App-image-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-image-spin-counter {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-180deg);
  }
}
